<template>
  <section>
    <div class="name">{{memberData.name}}</div>
    <!-- <div class="age" v-html="age"></div> -->
  </section>
</template>

<script>
  module.exports = {
   props: {
      memberData: Object
    },
    computed: {
      age: function(){
        var age = this.memberData.age.replace(/(\d+)/g, "<span>$1</span>");
        return age;
      }
    }
  };
</script>